.footer {
	@include css-lock(7,14,320,1024,padding-top);
	@include css-lock(33,66,320,1024,padding-bottom);
	padding: 14px;
	@include clr;
	&__agency {
		float: left;
		text-transform: uppercase;
		color: $black-l;
		letter-spacing: 0; }
	&__soc {
		float: right; } }

.soc {
	@include css-lock(0,5,320,1024,margin-top);
	font-size: 0;
	&__item {
		vertical-align: middle;
		display: inline-block;
		&:not(:first-child) {
			@include css-lock(15,30,320,1024,margin-left); } }
	&__link {
		display: block; } }
