.form {
	@include css-lock(35,70,320,1024,padding-top);
	@include css-lock(23,46,320,1024,padding-bottom);
	&__inner {
		@include css-lock(36,74,320,1024,padding-left);
		@include css-lock(36,74,320,1024,padding-right); } }
.form-control {
	&:not(:first-child) {
		@include css-lock(10,20,320,1024,margin-top); } }
.btn {
	@include css-lock(13,30,320,1024,padding-top);
	@include css-lock(13,30,320,1024,padding-bottom);
	@include css-lock(13,30,320,1024,padding-left);
	@include css-lock(13,30,320,1024,padding-right);
	width: 100%;
	color: $white;
	font-family: $Futuras;
	@include f(null,500,1);
	@include css-lock(14,30,320,1024);
	text-transform: uppercase;
	text-align: center;
	background-color: $amethystSmoke;
	@include css-lock(4,9,320,1024,border-radius);
	vertical-align: top;
	display: inline-block; }
.input {
	padding: 10px;
	width: 100%;
	@include css-lock(30,50,320,1024,height);
	color: $black-l;
	font-family: $Futuras;
	@include f(null,500,1);
	@include css-lock(14,30,320,1024);
	border-bottom: 1px solid rgba(#979797, 0.5);
	display: block;
	&::placeholder {
		color: rgba(#979797, 0.7); } }
