//FONTS
@mixin fontpath($name, $name1, $file) {
	src: local($name), local($name1), url("../fonts/" + $file + ".woff2") format("woff2"), url("../fonts/" + $file + ".woff") format("woff"); }
@mixin fontdefault($style, $weight, $name) {
	font-family: $name;
	font-style: $style;
	font-weight: $weight; }

// FuturaFuturis
@font-face {
	@include fontdefault(normal, 800, 'FuturaFuturis');
	@include fontpath('FuturaFuturis-Extrabold', 'FuturaFuturis-Extrabold', FuturaFuturis-Extrabold); }
@font-face {
	@include fontdefault(italic, 800, 'FuturaFuturis');
	@include fontpath('FuturaFuturis-Extrabold', 'FuturaFuturis-Extrabold', FuturaFuturis-Extrabold); }

@font-face {
	@include fontdefault(normal, 700, 'FuturaFuturis');
	@include fontpath('FuturaFuturis-Bold', 'FuturaFuturis-Bold', FuturaFuturis-Bold); }
@font-face {
	@include fontdefault(italic, 700, 'FuturaFuturis');
	@include fontpath('FuturaFuturis-BoldItalic', 'FuturaFuturis-BoldItalic', FuturaFuturis-BoldItalic); }

@font-face {
	@include fontdefault(normal, 500, 'FuturaFuturis');
	@include fontpath('FuturaFuturis-Medium', 'FuturaFuturis-Medium', FuturaFuturis-Medium); }
@font-face {
	@include fontdefault(italic, 500, 'FuturaFuturis');
	@include fontpath('FuturaFuturis-MediumItalic', 'FuturaFuturis-MediumItalic', FuturaFuturis-Medium); }

@font-face {
	@include fontdefault(normal, 300, 'FuturaFuturis');
	@include fontpath('FuturaFuturis-Light', 'FuturaFuturis-Light', FuturaFuturis-Light); }

