body {
	font-family: $Futuras;
	color: rgba($black, 0.5);
	@include f(null,500,1.3333);
	@include css-lock(14,30,320,1024);
	min-width: 320px;
	text-align: center;
	@include font-smoothing;
	letter-spacing: 0;
	background-color: $white; }

.inner {
	margin: 0 auto;
	@include css-lock(15,50,320,1024,padding-left);
	@include css-lock(15,50,320,1024,padding-right);
	max-width: 990px; }

.title {
	font-family: $Futuras;
	@include f(null,500,1.25);
	@include css-lock(25,50,320,1024);
	text-align: center;
	&_black {
		color: $black; } }
